/* =================================== Circle @mixin ================================== */
.circle { margin-top: 60px; }

.circle__main { position: relative; margin-left: auto; margin-right: auto; width: 433px; height: 433px; font-size: 94px; color: #3a3a3a; font-weight: 900; line-height: 0.42; border: 3px dotted #959595; border-radius: 50%; }

