.circle__main-number { position: relative; left: -5px; display: inline-block; min-width: 140px; text-align: center; vertical-align: middle; animation: animate-number 1s infinite alternate; }

.circle__main:after { content: ''; position: absolute; top: 20px; left: 20px; right: 20px; bottom: 20px; z-index: 1; border: 3px solid #a1a1a1; border-radius: 50%; pointer-events: none; }

