.circle__info_1:before { left: -50%; bottom: -11%; }

.circle__info_2 { bottom: 20%; right: -34%; }

.circle__info_2:before { bottom: -6px; left: -41%; }

.circle__info_3 { bottom: -20%; left: 50%; transform: translateX(-50%); }

.circle__info_3:before { top: -97%; left: 50%; margin-left: -10px; }

.circle__info_4 { bottom: 20%; left: -34%; }

.circle__info_4:before { bottom: -6px; right: -42%; }

.circle__info_5 { top: 3%; left: -30%; }

.circle__info_5:before { right: -40%; bottom: -11%; }

.circle__info:hover .circle__info-number, .circle__info:hover .circle__info-text, .circle__info.is-active .circle__info-number, .circle__info.is-active .circle__info-text { color: #42a4e1; }

